@import url(https://fonts.googleapis.com/css?family=Charm|Gloria+Hallelujah|Noto+Sans+JP|Roboto+Slab);

.main-menu{text-align:center}.main-menu>.text-padded{margin:10vh}.main-menu>img{max-width:60%;height:auto}.main-menu .button{border:6px solid black;display:block;margin:0.5em auto 0 auto}.main-menu .button:hover{background-color:#222;color:#eee;box-shadow:0 0 2em #bbb}.main-menu>h1,.main-menu h2{font-family:'Charm', cursive;font-weight:400}.main-menu>h1>span,.main-menu h2>span{font-family:cursive;font-weight:300}.main-menu>h1{font-size:2.5em;margin:20vh 0 0 0}.main-menu>h2{font-size:2.2em;margin:-0.5em 0 15vh 0}

:root{--mood-background-color: #fff;--mood-text-color: #111;--mood-disabled-color: #888;--mood-important-text-color: #ccccff}:root .button{background-color:Transparent;color:#111;color:var(--mood-text-color);border:solid 1px #111;border:solid 1px var(--mood-text-color);outline:none;overflow:visible;margin:9px 7px 9px 6.5px;width:160px;height:40px;max-width:100%;max-height:auto;transition:color 10ms;font-size:0.75em;font-family:'Noto Sans JP', Arial, Helvetica, monospace, sans-serif}:root .button.ready{border:solid 3px #111;border:solid 3px var(--mood-text-color)}:root .hidden{visibility:hidden}:root .title-button{border:1px solid black;display:block;margin:0.5em auto 0 auto}:root .title-button:hover{background-color:#222;color:#eee;box-shadow:0 0 2em #bbb}

.footer-container{display:flex;position:fixed;right:12px;bottom:12px;height:1em;font-size:0.7em}.footer-container>*{margin-left:0.25em}.footer-container>.footer-thanks{margin-top:-0.25em}

.cutscene-text{font-size:3em;font-family:'Gloria Hallelujah', cursive, monospace, sans-serif;position:absolute;top:50%;left:50%;transform:translateX(-50%) translateY(-50%)}

.action-panel-wrapper{display:flex;flex-wrap:wrap;align-items:flex-start;justify-content:flex-start}

.action-button-container{display:flex;flex-direction:column;margin:0 0.5rem 0 0;max-width:12rem}.action-button-subtext{font-size:0.8rem;align-self:center}

.information-panel-container{margin:0.5rem}.information-panel-divider{border:0.5px solid var(--mood-text-color);margin:0.5rem 2rem 0.5rem 2rem}.action-progress-bar{display:inline}

:root{--mood-background-color: #fff;--mood-text-color: #111;--mood-disabled-color: #888;--mood-important-text-color: #ccccff}:root body{color:#111;color:var(--mood-text-color);background-color:#fff;background-color:var(--mood-background-color);transition:background-color 2800ms cubic-bezier(0.39, 0.575, 0.565, 1),color 690ms ease-out;font-size:1.1em;font-family:'Roboto Slab', Courier New, Courier, monospace}:root .message-box{min-height:30vh;max-height:45.2vh;overflow:hidden;margin:12px 0px 0px 0px;padding:12px 12px 12px 12px;background-color:rgba(150,200,100,0.2)}:root .message-box .message.important{color:#ccccff;color:var(--mood-important-text-color)}:root .message-box .message.fadeout{opacity:1;-webkit-animation:fadeTextInOut 12000ms ease-in forwards;animation:fadeTextInOut 12000ms ease-in forwards}@-webkit-keyframes fadeOut{0%{opacity:1}100%{opacity:0;@keyframes fadeTextInOut{}}0%{opacity:0.2}9%{opacity:0.98}30%{opacity:0.94}45%{opacity:0.85}60%{opacity:0.6}100%{opacity:0}}@keyframes fadeOut{0%{opacity:1}100%{opacity:0;@-webkit-keyframes fadeTextInOut{}@keyframes fadeTextInOut{}}0%{opacity:0.2}9%{opacity:0.98}30%{opacity:0.94}45%{opacity:0.85}60%{opacity:0.6}100%{opacity:0}}:root .statusArea{line-height:110%}:root .hidden{visibility:hidden}:root .save-game-text{-webkit-animation:fadeOut 0.6s easein 1.5s 1 both;animation:fadeOut 0.6s easein 1.5s 1 both}

